import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import {  withRouter } from 'react-router-dom'
const styles = {
  root: {
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
};

class ButtonAppBar extends React.Component {
  state = {
    value: 0,
  };

  handleChange = (event, value) => {
    this.setState({ value });
     this.props.history.push(`/${value}`)
        
  }

 
 
  render(){
    const { classes } = this.props;
    const { value } = this.state;
    console.log(value)
    return (
      <div className={`${classes.root} `}>
        <AppBar  color='#000000'>
          <Toolbar >
            <IconButton onClick={this.props.OnMenuClick} className={classes.menuButton} color="inherit" aria-label="Menu">
       <MenuIcon />
            </IconButton>
            <Typography variant="h6" color="inherit" className={classes.grow}>
              Investox
            </Typography>
            <div className='dn dib-l  center'>
            <BottomNavigation
          value={value}
          onChange={this.handleChange}
          showLabels
          className={classes.root}

        >
          <BottomNavigationAction label="Home" value='Home'/>
          <BottomNavigationAction label="Enquiry" value='Enquiry'/>
          <BottomNavigationAction label="Contact Us" value='Contact Us'/>
          <BottomNavigationAction label="Terms"  value='Terms'/>
        </BottomNavigation>
   </div>
          </Toolbar>
        </AppBar>
      </div>
    );}
}

ButtonAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withRouter(ButtonAppBar));


