import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';


const styles = theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    marginBottom:theme.spacing.unit,
    width: 200,
  },
  dense: {
    marginTop: 19,
  },
  menu: {
    width: 200,
  },
  formControl: {
    margin: theme.spacing.unit,
    minWidth: 120,
    maxWidth: 300,
  },
});

 class Form extends Component {
 	state={
 		expext:''
 	}
 	 expextChange = event => {
    this.setState({ value: event.target.value });
  };
	render() {
		const { classes } = this.props;
		return (
			<div className='ml3-l w-50'  >
				
        <TextField
          id="standard-with-placeholder"
          label=" Name"
          placeholder="Name"
          className={classes.textField}
          margin="normal"
        />
        
        <br/>
        
         <br/>
        
        <br/>
       
        <TextField
          id="standard-with-placeholder"
          placeholder="Mobile Number"
          type='number'
          className={classes.textField}
          margin="normal"
        />
        <br/>
       
        <br />
        <TextField
          id="standard-with-placeholder"
          label="Email"
          placeholder="some@other.com"
          className={classes.textField}
          margin="normal"
        />
        <br />
        <TextField
          id="standard-with-placeholder"
          label="Subject"
          className={classes.textField}
          margin="normal"
        /> <br />

        <TextField
          id="standard-with-placeholder"
          label="Address"
         
          className={classes.textField}
          margin="normal"
        /> <br />
          <TextField
          id="standard-multiline-flexible"
          label="Your message"
          multiline
          rowsMax="4"
          value={this.state.multiline}
        
          className={classes.textField}
          margin="normal"
        />
        <div className='pb5 pt3'><Button color="primary" variant="contained">     
		Submit
      </Button>
      </div>
			</div>
		);
	}
}

export default withStyles(styles)(Form)


