import React from 'react'
import Button from '@material-ui/core/Button';

const Section1=(props)=>{
	return(
		<div className='bg-white section1 center tc br3 mb0'>
		<h3 className='pa2 pt4'> Trusted Stock Market Training Company</h3>
		<div className='tc center pa2 w-70 '>Our priority here at investox is to provide the market knowledge and trading experience our clients need in order to achieve the financial investment success they deserve. Our professional trainers make stock trading simple and straightforward. Get in touch with us for more information and to discuss your trading goals.</div>
		<h2 className='tc pa3 pb2'>INVESTOX</h2>
		<div className='pb5'><Button color="primary" variant="contained" onClick={()=>props.onReserve("reserve")}>     
		 Reserve a seat
      </Button>
      </div>
  
		</div>
		)
}
export default Section1;


