import React, { Component } from 'react';
import './App.css';
import 'tachyons';
import Footer from './Component/Footer'
import Home from './Component/Home'
import ButtonAppBar from './Component/Home/btn'
import Enquiry from './Component/Enquiry'
import Drawer from './Component/sideDrawer'
import { Route , Switch} from "react-router-dom";
import ContactUs from './Component/ContactUs'
import Terms from './Component/Terms'
class App extends Component {
 
  	constructor(){
  		super();
  		this.state={
  		drawer:false
  		}
  	}
  	
    OnMenuClick=()=>{
      this.setState({drawer:!this.state.drawer})

    }
    
  	 render() {
  	 	
    return (
      <div className='App'>
      <ButtonAppBar OnMenuClick={this.OnMenuClick}/>
    <Drawer drawer={this.state.drawer} OnMenuClick={this.OnMenuClick}/>
    <Switch>
    
    <Route path='/Enquiry' exact component={Enquiry} />

    <Route path='/Contact Us' exact component={ContactUs} />

    <Route path='/Terms' exact component={Terms} />
      <Route path='/' 
           render={(props)=>(<Home {...props} OnMenuClick={this.OnMenuClick} drawer={this.state.drawer}/>)}/>  
    
    </Switch>
       <Footer/>
      </div>
    );
  }
}


export default App;



