import React from 'react'
import Card from './card'
const cardDetail=[
{
	url:'',
	title:'STOCK MARKET TRANING',
	text:'We provide all our clients with Services using leading financia principles. The outcomes is an extremely diversified, high-yeild portfolio.'
},
{
	url:'',
	title:'FINANCIAL TRANING',
	text:'We provide all our clients with Services using leading financia principles. The outcomes is an extremely diversified, high-yeild portfolio.'
},
{
	url:'',
	title:'WEALTH MANAGEMENT TRANING',
	text:'We provide all our clients with Services using leading financia principles. The outcomes is an extremely diversified, high-yeild portfolio.'
},]
const Section2=()=>{
	
	return(
		<div className='bg-navy section2 ma0 center tc mb0'>
		<h3 className='tc white pa3 fw4 pt5'>Services</h3>
			<div className='center flex-l flex-m pa3 w-70-l pb4'>
					<Card url={cardDetail[0].url} title={cardDetail[0].title} text={cardDetail[0].text}/>
					<Card url={cardDetail[1].url} title={cardDetail[1].title} text={cardDetail[1].text}/>
					<Card url={cardDetail[2].url} title={cardDetail[2].title} text={cardDetail[2].text}/>
			</div>
		</div>
		)
}
export default Section2;


