import React, { Component } from 'react';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';


const styles = theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    marginBottom:theme.spacing.unit,
    width: 200,
  },
  dense: {
    marginTop: 19,
  },
  menu: {
    width: 200,
  },
  formControl: {
    margin: theme.spacing.unit,
    minWidth: 120,
    maxWidth: 300,
  },
});

 class Form extends Component {
 	state={
 		expext:''
 	}
 	 expextChange = event => {
    this.setState({ value: event.target.value });
  };
	render() {
		const { classes } = this.props;
		return (
			<div className='ml6-l'>
				
        <TextField
          id="standard-with-placeholder"
          label="First Name"
          placeholder="Name"
          className={classes.textField}
          margin="normal"
        />
         <TextField
          id="standard-with-placeholder"
          label="Last Name"
          placeholder="Name"
          className={classes.textField}
          margin="normal"
        />
        <br/>
        <TextField
          id="standard-with-placeholder"
          label="Country"
          placeholder="Your Country"
          className={classes.textField}
          margin="normal"
        />
         <br/>
        <TextField
          id="standard-with-placeholder"
          label="City"
         type='text'
          className={classes.textField}
          margin="normal"
        />
        <br/>
        <div className='ma3 ml0'>
        <TextField
          id="standard-with-placeholder"
          placeholder="Mobile Number"
          type='number'
          className={classes.textField}
          margin="normal"
        /></div>
        <br/>
        <TextField
          id="standard-with-placeholder"
          placeholder="Whatsapp Number"
          type='number'
          className={classes.textField}
          margin="normal"
        />
        <br />
        <TextField
          id="standard-with-placeholder"
          label="Email"
          placeholder="some@other.com"
          className={classes.textField}
          margin="normal"
        />
        <br />
    	<div className='mt4 mb0'>
     <FormControl component="fieldset" className={classes.formControl}>
          <FormLabel component="legend">What do you expect from stock market?</FormLabel>
          <RadioGroup
            aria-label=""
            name="gender2"
            className={classes.group}
            value={this.state.expect}
            onChange={this.expextChange}
          >
            <FormControlLabel
              value="Regular source of income"
              control={<Radio color="primary" />}
              label="Regular source of income"
            
            />
            <FormControlLabel
              value="Pocket money"
              control={<Radio color="primary" />}
              label="Pocket money"
          
            />
            <FormControlLabel
              value="An extra source of income"
              control={<Radio color="primary" />}
              label="An extra source of income"
         
            />
            <FormControlLabel
              value="Gambling"
              control={<Radio />}
              label="Gambling"
       
            />
          </RadioGroup>
        </FormControl></div>  <br /><div className='mt2 mb0'>
            <FormControl component="fieldset" className={classes.formControl}>
          <FormLabel component="legend">Do you want our certified financial trainer's support to become an expert?</FormLabel>
          <RadioGroup
            aria-label=""
            name="gender2"
            className={classes.group}
            value={this.state.expect}
            onChange={this.expextChange}
          >
            <FormControlLabel
              value="Yes"
              control={<Radio color="primary" />}
              label="Yes"
              
            />
            <FormControlLabel
              value="No"
              control={<Radio color="primary" />}
              label="No"
              
            />
            <FormControlLabel
              value="May be"
              control={<Radio color="primary" />}
              label="May be"
              
            />
          
          </RadioGroup>
        </FormControl></div>   <br /><div className='mt2 mb0'>
            <FormControl component="fieldset" className={classes.formControl}>
          <FormLabel component="legend">How much investment you are expecting to start with?</FormLabel>
          <RadioGroup
            aria-label=""
            name="gender2"
            className={classes.group}
            value={this.state.expect}
            onChange={this.expextChange}
          >
            <FormControlLabel
              value="5000 to 10000"
              control={<Radio color="primary" />}
              label="5000 to 10000"
              
            />
            <FormControlLabel
              value="10000 to 25000"
              control={<Radio color="primary" />}
              label="10000 to 25000"
              
            />
            <FormControlLabel
              value="25000 and above"
              control={<Radio color="primary" />}
              label="25000 and above"
              
            />
          </RadioGroup>
        </FormControl></div> 
        <br/>
         <FormControl className={classes.formControl}>
          <FormLabel component="legend">How did you hear about us?</FormLabel>
          <Select
            value={this.state.age}
            onChange={this.handleChange}
            inputProps={{
          
            }}
          >
            <MenuItem value="Linkedin">Linkedin</MenuItem>
            <MenuItem value='Youtube'>Youtube</MenuItem>
            <MenuItem value='Instagram'>Instagram</MenuItem>
            <MenuItem value='Google'>Google</MenuItem>
          </Select>
        </FormControl>
        <div className='pb5 pt3'><Button color="primary" variant="contained">     
		Submit
      </Button>
      </div>
			</div>
		);
	}
}

export default withStyles(styles)(Form)


